.container {
  margin: 0 30px;
}

.relative {
  position: relative;
}

.flex {
  &--end {
    display: flex;
    justify-content: flex-end;
  }

  &--tablet {
    @include onTablet {
      display: flex;
      justify-content: space-between;
    }
  }
}

.max-width--1200 {
  max-width: 1200px;
  margin: 0 auto;
}
